* {
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
  padding: 20px;
  background-image: url('ttc.jpg');
  background-blend-mode: color-burn;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  min-width: 100vw;

}

body::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url('ttc.jpg');
  background-blend-mode: color-burn;
  opacity: 0.2; /* Sesuaikan dengan tingkat kejernihan yang diinginkan (antara 0 dan 1) */
  z-index: -1;
}

.square {
  width: 60px;
  height: 60px;
  border: 2px solid black;
  background-color: #fff;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
}

.board {
  display:flex;
  flex-wrap: wrap;
  width: 180px;
  height: 180px;
}
.game {
  display: flex;
}
.status{
  margin-bottom: 5px;
}
